.App {
  text-align: center;
}

.mainBox {
  display: flex;
  align-items: center;
  flex-direction: column;
}

body {
  background-color: black;
  color: wheat;
}

.mainBox a {
  color: wheat;
}

.mainBox a:hover {
  color: lightblue;
}

.navbar {
  margin: 0px;
  padding: 20px;
  border-radius: 30px;
  background-color: #333;
  display: flex;
  justify-content: space-evenly;
  font-family: Arial, Helvetica, sans-serif;
}

.navbar a {
  color: white;
  font-size: 20px;
  margin: 20px;
  text-decoration: none;
}

.navbar a:hover {
  color: lightblue;
}

.image {
  width: 99%;
  max-width: 1024px;
  display: block;
  margin: auto;
}

.textblock {
  max-width: 600px;
  width: 80%;
  display: block;
  margin: auto;
}

.splitbox {
  display: flex;
  margin: 20px;
  justify-content: space-evenly;
}
.rightbox {
  align-self: center;
}

.links {
  margin: 10px;
  max-width: 600px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.ContactForm {
  width: 600px;
}

form {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  font-size: 16px;

  input {
    width: 100%;
    height: 35px;
    padding: 7px;
    outline: none;
    border-radius: 5px;
    border: 1px solid rgb(220, 220, 220);

    &:focus {
      border: 2px solid rgba(0, 206, 158, 1);
    }
  }

  textarea {
    max-width: 100%;
    min-width: 100%;
    width: 100%;
    max-height: 100px;
    min-height: 100px;
    padding: 7px;
    outline: none;
    border-radius: 5px;
    border: 1px solid rgb(220, 220, 220);

    &:focus {
      border: 2px solid rgba(0, 206, 158, 1);
    }
  }

  label {
    margin-top: 1rem;
  }

  input[type="submit"] {
    margin-top: 2rem;
    cursor: pointer;
    background: rgb(206, 168, 0);
    color: black;
    border: none;
  }
}

span {
  color: red;
}
